<template>
  <div class="user">
    <el-form :inline="true" :model="formInline" class="demo-form-inline">
      <el-form-item label="姓名">
        <el-input v-model="formInline.user" placeholder="姓名"></el-input>
      </el-form-item>
      <el-form-item label="手机号">
        <el-input v-model="formInline.pwd" placeholder="手机号"></el-input>
      </el-form-item>
      <el-form-item label="用户状态">
        <el-select v-model="formInline.region" placeholder="用户状态">
          <el-option label="启用" value="启用"></el-option>
          <el-option label="禁止" value="禁用"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">添加</el-button>
      </el-form-item>
    </el-form>

    <!-- 表格 -->
    <template>
      <el-table
        ref="multipleTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 80%,margin: auto;"
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection"></el-table-column>
        <el-table-column prop="user" label="姓名"> </el-table-column>
        <el-table-column prop="pwd" label="电话" show-overflow-tooltip>
        </el-table-column>
        <el-table-column prop="region" label="状态" show-overflow-tooltip>
        </el-table-column>
        <el-table-column label="操作" show-overflow-tooltip>
          <template slot-scope="scope">
            <div>
              <el-button
                type="primary"
                icon="el-icon-delete"
                @click="sc(scope.$index)"
              ></el-button>
              <el-button
                type="primary"
                @click="
                  bj(scope.$index, scope.row), (dialogTableVisible = true)
                "
                >编辑</el-button
              >
              <!-- {{ scope.row }} -->
            </div>
          </template>
        </el-table-column>
      </el-table>
      <div style="margin-top: 20px">
        <el-button @click="toggleSelection([tableData[1], tableData[2]])"
          >切换第二、第三行的选中状态</el-button
        >
        <el-button @click="toggleSelection()">取消选择</el-button>
      </div>
    </template>

    <!-- 表格 -->
    <el-dialog title="编辑" :visible.sync="dialogTableVisible">
      <el-form :inline="true" class="demo-form-inline">
        <el-form-item label="姓名">
          <el-input v-model="user" placeholder="姓名"></el-input>
        </el-form-item>
        <el-form-item label="手机号">
          <el-input v-model="pwd" placeholder="手机号"></el-input>
        </el-form-item>
        <el-form-item label="用户状态">
          <el-select v-model="region" placeholder="用户状态">
            <el-option label="启用" value="启用"></el-option>
            <el-option label="禁止" value="禁用"></el-option>
          </el-select>
        </el-form-item>
        <p>
          <el-form-item>
            <el-button type="primary" @click="gg">更改</el-button>
            <el-button type="primary" @click="dialogTableVisible = false"
              >取消</el-button
            >
          </el-form-item>
        </p>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      user: "",
      region: "",
      pwd: "",
      index: "",
      dialogTableVisible: false,
      formInline: {
        user: "",
        region: "",
        pwd: "",
      },
      tableData: [],
      multipleSelection: [],
    };
  },
  mounted() {
    console.log(this.$store.state.list);
    this.tableData = this.$store.state.list;
  },
  methods: {
    gg() {
      let val = {
        user: this.user,
        region: this.region,
        pwd: this.pwd,
      };
      this.$store.commit("bj", { val, index: this.index });
      this.$router.go(0);
    },
    bj(index, row) {
      this.index = index;
      console.log(row);
      this.user = row.user;
      this.pwd = row.pwd;
      this.region = row.region;
    },
    sc(index) {
      // console.log(index);
      this.$store.commit("sc", index);
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach((row) => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    onSubmit() {
      // console.log(this.formInline.region);
      if (
        this.formInline.region == "" &&
        this.formInline.user == "" &&
        this.formInline.pwd == ""
      ) {
        alert("您还没有填信息");
      } else {
        if (this.formInline.region == "") {
          alert("状态不能空");
        } else {
          if (this.formInline.user == "") {
            alert("用户名不能为空");
          } else {
            if (this.formInline.user == "") {
              alert("手机号不能为空");
            } else {
              this.$store.commit("add", {
                region: this.formInline.region,
                pwd: this.formInline.pwd,
                user: this.formInline.user,
              });
            }
          }
        }
      }
      this.formInline.region = "";
      this.formInline.pwd = "";
      this.formInline.user = "";
    },
  },
};
</script>

<style lang="scss"></style>
